import { CompareResult } from './types.js';
import { clean, compareVersion, validateWithError } from './utils.js';

/**
 * @description
 * Determines if version `verA` is greater than version `verB`.
 *
 * @param verA - The first version to compare.
 * @param verB - The second version to compare.
 * @returns `true` if `verA` is greater than `verB`, `false` otherwise.
 *
 * @docsCategory preference/utils/semver
 * @docsPage semver-gt
 * @codeFilter gt
 */
export const gt = (verA: string, verB: string): boolean => {
  if (validateWithError(verA, verB)) {
    return (
      compareVersion(clean(verA), clean(verB)) === CompareResult.GreaterThan
    );
  }
  return false;
};
